.vds-chip-container {

  .vds-chip {
    .vds-btn-icon.focus-visible {
      outline: 0;
    }
  }

  .vds-chip-input {
    flex: 1;
    height: 28px;
    min-width: 300px;
    border: 0;
    font-size: 14px;
    height: 30px;
    margin-bottom: 0.5rem;

    &:focus {
      outline: 0;
    }
  }

  &.vds-chip-type-input {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;
    padding: 0.5rem 0.5rem 0;
    border: 1px solid #616188;

    &.vds-state--focus {
      border-color: #0051dc;
      box-shadow: 0 1px 0 0 #0051dc;
    }
  }

  .vds-chip-list-container {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
  }
}
